p {
  padding: 0;
  margin: 0;
}

.flex {
  display: flex;
}

.posts {
  margin-bottom: 100px;
  flex-direction: column;
}

.post {
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #4872a3;
  border-radius: 10px;
}

.post p {
  margin-bottom: 10px;
  font-size: 20px;
  color: #333;
}